import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import {
  getUserdata,
  localStoragedecryp,
} from "../../../../components/helpers/function";
class LaporanEodExel extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan EOD"
          sheet="Laporan EOD"
          buttonText="Export Exel"
        />
        <table
          rowkey="kode_member"
          id="table-to-xls"
          style={{ display: "none" }}
        >
          <thead>
            <tr>
              <th colSpan="10" style={{ textAlign: "center" }}>
                {" "}
                LAPORAN EOD
              </th>
            </tr>
            <tr>
              <th colSpan="10" style={{ textAlign: "center" }}>
                {" "}
                Tanggal{" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tanggal}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="10"> TOKO : {getUserdata().nama_toko} </th>
            </tr>

            <tr>
              <th colSpan="10"> ALAMAT : {getUserdata().alamat_toko} </th>
            </tr>
            <tr>
              <th colSpan="10" style={{ textAlign: "center" }}></th>
            </tr>
          </thead>
          <body>
            <tr>
              <th
                colSpan="2"
                style={{ backgroundColor: "#E8E5E5", color: "#000" }}
              >
                Kategori{" "}
              </th>
              <th
                colSpan="2"
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                Qty{" "}
              </th>
              <th
                colSpan="2"
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                Berat{" "}
              </th>
              <th
                colSpan="2"
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              >
                Jumlah{" "}
              </th>
              <th
                colSpan="2"
                style={{
                  backgroundColor: "#E8E5E5",
                  color: "#000",
                  textAlign: "right",
                }}
              ></th>
            </tr>
            {this.props.data.length === 0
              ? null
              : this.props.data.EODMasuk.map((row, index) => (
                  <tr key={index}>
                    <td colSpan="2">{row.kategori}</td>
                    <td colSpan="2" style={{ textAlign: "right" }}>
                      &nbsp;{row.qty}
                    </td>
                    <td colSpan="2" style={{ textAlign: "right" }}>
                      &nbsp;
                      {row.berat === "-" || null ? "-" : row.berat.toFixed(2)}
                    </td>
                    <td colSpan="2" style={{ textAlign: "right" }}>
                      &nbsp;
                      {row.jumlah === "-" || null
                        ? "-"
                        : row.jumlah.toLocaleString("kr-KO")}
                    </td>
                    <td colSpan="2"></td>
                  </tr>
                ))}
            {this.props.data.length === 0 ? null : (
              <tr>
                <td colSpan="8" style={{ textAlign: "center" }}> SUBTOTAL (RP) </td>
                <td colSpan="2" style={{ textAlign: "right" }}>&nbsp; {this.props.data.EODMasuk.reduce((a,b)=>a+b.jumlah,0).toLocaleString("ko-KO")} </td>
              </tr>
            )}
            {this.props.data.length === 0
              ? null
              : this.props.data.EODKeluar.map((row, index) => (
                  <tr key={index}>
                    <td colSpan="2">{row.kategori}</td>
                    <td colSpan="2" style={{ textAlign: "right" }}>
                      &nbsp;{row.qty}
                    </td>
                    <td colSpan="2" style={{ textAlign: "right" }}>
                      &nbsp;
                      {row.berat === "-" || null ? "-" : row.berat.toFixed(2)}
                    </td>
                    <td colSpan="2" style={{ textAlign: "right" }}>
                      &nbsp;
                      {row.jumlah === "-" || null
                        ? "-"
                        : row.jumlah.toLocaleString("kr-KO")}
                    </td>
                    <td colSpan="2"></td>
                  </tr>
                ))}
            {this.props.data.length === 0 ? null : (
              <tr>
                <td colSpan="8" style={{ textAlign: "center" }}> SUBTOTAL (RP) </td>
                <td colSpan="2" style={{ textAlign: "right" }}> &nbsp;{this.props.data.EODKeluar.reduce((a,b)=>a+b.jumlah,0).toLocaleString("ko-KO")} </td>
              </tr>
            )}
            {this.props.data.length === 0 ? null : (
              <tr>
                <td colSpan="8" style={{ textAlign: "center" }}> TOTAL (RP) </td>
                <td colSpan="2" style={{ textAlign: "right" }}> &nbsp; { (this.props.data.EODMasuk.reduce((a,b)=>a+parseInt(b.jumlah),0) + this.props.data.EODKeluar.reduce((a,b)=>a+parseInt(b.jumlah),0)).toLocaleString("ko-KO") } </td>
              </tr>
            )}
          </body>
          <tfoot></tfoot>
        </table>
      </>
    );
  }
}
export default LaporanEodExel;
