import jsPDF from "jspdf";
import "jspdf-autotable";
import {
  getToday,
  getUserdata,
  localStoragedecryp,
} from "../../../../components/helpers/function";
// Date Fns is used to format the dates we receive

// define a generatePDF function that accepts a tickets argument
const CetakLaporanEod = (data = "") => {
  // initialize jsPDF
  const doc = new jsPDF("P", "mm", [297, 210]);
  //   let data = JSON.parse(localStorage.getItem("tt_pengeluaran_barang")) || [];
  let tableRows = [];
  let finalY = 30;
  let tableColumn = [];

  doc.setFontSize(15);
  doc.text("LAPORAN EOD", 14, 15);
  doc.setFontSize(20);
  doc.text(getUserdata().nama_toko, 130, 15);
  doc.setFontSize(8);
  let jml_alamat = getUserdata().alamat_toko.length;
  if (jml_alamat > 20) {
    doc.text(getUserdata().alamat_toko.slice(0, 48), 130, 20);
  }
  if (jml_alamat > 50) {
    doc.text(getUserdata().alamat_toko.slice(48, 90), 130, 25);
  }
  doc.setFontSize(10);
  doc.setProperties({
    title: "LAPORAN EOD",
  });

  //row 1
  doc.text(
    `PERIODE : ${
      localStoragedecryp("tgl_laporan").length === 0
        ? null
        : JSON.parse(localStoragedecryp("tgl_laporan")).tanggal
    }`,
    14,
    25
  );

        tableColumn = [
          [
            {
              content: `Kategori`,
              styles: {
                fillColor: "#E8E5E5",
                textColor: "#000",
              },
            },
            {
              content: `Qty`,
              styles: {
                halign: "right",
                fillColor: "#E8E5E5",
                textColor: "#000",
              },
            },
            {
              content: `Berat`,
              styles: {
                halign: "right",
                fillColor: "#E8E5E5",
                textColor: "#000",
              },
            },
            {
              content: `Jumlah`,
              styles: {
                halign: "right",
                fillColor: "#E8E5E5",
                textColor: "#000",
              },
            },
            {
              content: ``,
              styles: {
                halign: "right",
                fillColor: "#E8E5E5",
                textColor: "#000",
              },
            },
          ],
        ];

        data.EODMasuk.forEach(item => {
          let rows = [ 
            {
              content : item.kategori,
              styles : {
                columnWidth : "10",
                lineWidth: 0.02,
                lineColor: "#000",
              }
            },
            {
              content : item.qty,
              styles: {
                halign: "right",
                lineWidth: 0.02,
                lineColor: "#000",
              },
            },
            {
              content : item.berat === "-" || null ? '-' : item.berat.toFixed(2),
              styles: {
                halign: "right",
                lineWidth: 0.02,
                lineColor: "#000",
              },
            },
            {
              content : item.jumlah.toLocaleString("ko-KO"),
              styles: {
                halign: "right",
                lineWidth: 0.02,
                lineColor: "#000",
              },
            },
            {
              content : '',
              styles: {
                halign: "right",
                lineWidth: 0.02,
                lineColor: "#000",
              },
            },
           
          ]
          tableRows.push(rows)
        });
        let subotal = [
          {
            colSpan: 4,
            content: `SUBTOTAL (RP)`,
            styles: {
              halign: "center",
              lineWidth: 0.02,
              fontStyle: "italic",
              lineColor: "#000",
            },
          },
          {
            content: data.EODMasuk.reduce((a,b)=>a+b.jumlah,0).toLocaleString("ko-KO"),
            styles: {
              lineWidth: 0.02,
              lineColor: "#000",
              halign: "right",
              fontStyle: "italic",
              textColor: "#000",
            },
          },
        ]
        tableRows.push(subotal);

        data.EODKeluar.forEach(item => {
          let rows = [ 
            {
              content : item.kategori,
              styles : {
                columnWidth : "10",
                lineWidth: 0.02,
                lineColor: "#000",
              }
            },
            {
              content : item.qty,
              styles: {
                halign: "right",
                lineWidth: 0.02,
                lineColor: "#000",
              },
            },
            {
              content : item.berat === "-" || null ? '-' : item.berat.toFixed(2),
              styles: {
                halign: "right",
                lineWidth: 0.02,
                lineColor: "#000",
              },
            },
            {
              content : item.jumlah.toLocaleString("ko-KO"),
              styles: {
                halign: "right",
                lineWidth: 0.02,
                lineColor: "#000",
              },
            },
            {
              content : '',
              styles: {
                halign: "right",
                lineWidth: 0.02,
                lineColor: "#000",
              },
            },
           
          ]
          tableRows.push(rows)
        });
        let subotal2 = [
          {
            colSpan: 4,
            content: `SUBTOTAL (RP)`,
            styles: {
              halign: "center",
              lineWidth: 0.02,
              fontStyle: "italic",
              lineColor: "#000",
            },
          },
          {
            content: data.EODKeluar.reduce((a,b)=>a+b.jumlah,0).toLocaleString("ko-KO"),
            styles: {
              lineWidth: 0.02,
              lineColor: "#000",
              halign: "right",
              fontStyle: "italic",
              textColor: "#000",
            },
          },
        ]
        tableRows.push(subotal2);


      
  // no = 1;
  let hasil = data.EODKeluar.reduce((a,b)=>a+b.jumlah,0) + data.EODMasuk.reduce((a,b)=>a+b.jumlah,0)
  let Grand = [
    {
      colSpan: 4,
      content: `TOTAL (RP)`,
      styles: {
        // lineWidth: 0.1,
        halign : "center",
        fontStyle: "italic",
        lineWidth: 0.02,
        lineColor: "#000",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
    {
      content: hasil.toLocaleString("kr-KO"),
      styles: {
        // lineWidth: 0.1,
        halign : "right",
        fontStyle: "italic",
        lineWidth: 0.02,
        lineColor: "#000",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(Grand);

  let printed = [
    {
      colSpan: 3,
      content: `Printed By ${JSON.parse(localStoragedecryp("userdata")).user_id} / ${getToday()}`,
      styles: {
        // lineWidth: 0.1,
        fontStyle: "italic",
        textColor: "#000",
        // fillColor: "#E8E5E5"
      },
    },
  ]
  tableRows.push(printed);
  doc.autoTable({
    head: tableColumn,
    body: tableRows,
    // foot: footRows,
    startY: finalY,
    theme: "plain",
    // rowPageBreak: "avoid",
    // pageBreak: "avoid",
    margin: { top: 15 },
    footStyles: {
      fontSize: 8,
    },
    bodyStyles: {
     
      fontSize: 8,
    },
    headStyles: {
      fontSize: 8,
      lineWidth: 0.02,
      lineColor: "#000",
      textColor: "#000",
      fillColor: "#E8E5E5",
    },
  });
  finalY = doc.autoTableEndPosY() + 3;
  tableRows = [];
  // const date = Date().split(" ");
  // we use a date string to generate our filename.
  // const dateStr = date[2] + date[3] + date[4];
  // ticket title. and margin-top + margin-left

  // doc.text(
  //   `User                        : ${
  //     JSON.parse(localStoragedecryp("userdata")).user_id
  //   }`,
  //   14,
  //   finalY + 10
  // );
  // doc.text(`Cetak                      : ${getToday()}`, 14, finalY + 16);
  const pages = doc.internal.getNumberOfPages();
  const pageWidth = doc.internal.pageSize.width; //Optional
  const pageHeight = doc.internal.pageSize.height; //Optional
  doc.setFontSize(10); //Optional
  for (let j = 1; j < pages + 1; j++) {
    let horizontalPos = pageWidth / 2; //Can be fixed number
    let verticalPos = pageHeight - 10; //Can be fixed number
    doc.setPage(j);
    doc.text(`${j} of ${pages}`, horizontalPos, verticalPos, {
      align: "center",
    });
  }
  // doc.autoPrint();
  // doc.save(`Laporan BARANG SUMARY.pdf`);
  var string = doc.output("datauristring");
  // var embed = "<embed width='100%' height='100%' src='" + string + "'/>";
  var x = window.open();
  x.document.open();
  x.document.write(
    "<html><head><title>LAPORAN EOD</title></head><body style='margin:0 !important'><embed width='100%' height='100%'  src=" +
      string +
      "></embed></body></html>"
  );
  // x.document.write(embed);
  // setInterval(() => {
  //   x.close();
  // }, 1000);
};

export default CetakLaporanEod;
