import React, { Component } from "react";

import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import { InputDate } from "../../../components/helpers/field";
import { getTglSystem } from "../../../components/helpers/function";
import Tabel from "../../../components/helpers/tabel";
import ValidasiTanggalLaporan from "../../../Validasi/ValidasiTanggalLaporan";
import CetakLaporanEod from "./pdf/CetakLaporanEod";
// import Loading from 'react-fullscreen-loading';
import LaporanEodExel from "./exel/LaporanEodExel";

class FilterLaporanEod extends Component {
  constructor(props) {
    super(props);
    this.state = {
      date: new Date(),
      columns : [
        {
          dataField: "kategori",
          text: "Kategori",
          footer: "",
        },
  
        {
          dataField: "qty",
          text: "Qty",
          headerClasses: "text-right",
          formatter: (data) => {
            return (
              <div className="text-right">
               {" "}
                {data === '-' || null
                  ? '-'
                  : data}
              </div>
            );
          },
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {columnData.reduce((acc, item) => acc + (item==="-" ? 0 : item), 0)}{" "}
            </div>
          ),
        },
        {
          dataField: "berat",
          text: "Berat",
          headerClasses: "text-right",
          formatter: (data) => {
            return (
              <div className="text-right">
                {" "}
                {data === '-' || null
                  ? '-'
                  : data.toFixed(2)}
              </div>
            );
          },
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {columnData.reduce((acc, item) => acc + (item==="-" ? 0 : item), 0).toFixed(2)}{" "}
            </div>
          ),
        },
        {
          dataField: "jumlah",
          text: "Jumlah",
          headerClasses: "text-right",
          formatter: (data) => {
            return (
              <div className="text-right">
                {" "}
                {data === undefined || null
                  ? 0
                  : data.toLocaleString("ko-KO")}
              </div>
            );
          },
          footer: (columnData) => (
            <div className="text-right">
              {" "}
              {columnData.reduce((acc, item) => acc + item, 0).toLocaleString("ko-KO")}{" "}
            </div>
          ),
        },
  
      ]
    };
    // this.handleSubmit = this.handleSubmit.bind(this);
    this.setStartDate = this.setStartDate.bind(this);
    this.setLastDate = this.setLastDate.bind(this);
  }

  exportPdf() {
    CetakLaporanEod(this.props.LaporanEod);
  }
  setStartDate(date) {
    this.setState({
      tgl_awal: new Date(date),
    });
  }
  setLastDate(date) {
    this.setState({
      tgl_akhir: new Date(date),
    });
  }

  render() {
    return (
      <form onSubmit={this.props.handleSubmit} autoComplete="off">
        <div className="row">
          <div className="col-lg-6">
            <Field
              name="tgl_awal"
              component={InputDate}
              label="Tanggal"
              type="text"
              defaultValue={getTglSystem()}
              selected={this.state.tgl_awal}
              onChange={(date) => this.setStartDate(date)}
              placeholder="Masukan Tanggal Dari"
            />
          </div>

          <div className="col-lg-6">
            <div className="text-right">
              <label>&nbsp;</label>
              <button
                type="submit"
                value="PDF"
                name="type_btn"
                className="btn btn-primary btn-block"
              >
                {this.props.isLoading ? (
                  <>
                    <i className="fas fa-spinner fa-spin"></i> &nbsp; Sedang
                    Mencari
                  </>
                ) : (
                  "Cari Laporan"
                )}
              </button>
            </div>
          </div>
          <div className="col-12">
            <Tabel
              keyField="kode_group"
              data={this.props.export ? this.props.LaporanEod.EODMasuk : []}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Laporan EOD Masuk"
            />
          </div>
          <div className="col-12">
            <Tabel
              keyField="kode_group"
              data={this.props.export ? this.props.LaporanEod.EODKeluar : []}
              columns={this.state.columns}
              empty={true}
              textEmpty="Data Laporan EOD Keluar"
            />
          </div>
          <div className={this.props.export ? "col-lg-12" : "col-lg-12 d-none"}>
            {/* <label htmlFor="">&nbsp;</label> */}
            <div className="row">
              <div className="col-lg-6">
                <button
                type="button"
                  onClick={() => this.exportPdf()}
                  className="btn btn-warning btn-block"
                >
                  {" "}
                  Export PDF{" "}
                </button>
              </div>
              <div className="col-lg-6">
                <LaporanEodExel data={this.props.export ? this.props.LaporanEod : []} />
              </div>
            </div>
          </div>
        </div>
         {/* {/* {this.state.isLoading ? (
        <Loading 
        loading
        background="rgba(0, 0, 0, 0.35)"
         loaderColor="rgba(94, 147, 117, 1)" />
      ) : null} */}
      </form>
    );
  }
}

FilterLaporanEod = reduxForm({
  form: "FilterLaporanEod",
  enableReinitialize: true,
  validate: ValidasiTanggalLaporan,
})(FilterLaporanEod);
export default connect((state) => {
  return {
    initialValues: {
      tgl_awal: getTglSystem(),
      // tgl_akhir: getTglSystem(),
    },
  };
})(FilterLaporanEod);
