import React from "react";
import { connect } from "react-redux";
import { Link } from "react-router-dom";
import { reduxForm } from "redux-form";
import { formatDate, localStorageencryp } from "../../../components/helpers/function.jsx";
import { ToastNotification } from "../../../components/helpers/notification.jsx";
import { Panel, PanelHeader } from "../../../components/panel/panel.jsx";
import { postDataNoTImeOut } from "../../../config/axios.jsx";
import FilterLaporanEod from "./FilterLaporanEod.jsx";
import * as moment from "moment-timezone";


class FormLaporanEod extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      isLoading: false,
      LaporanEod: [],
      export: false,
    };
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleSubmit(data) {
    this.setState({
      isLoading: true,
    });
    let data_post = {
      tanggal: formatDate(moment(new Date(data.tgl_awal)).format("YYYY-MM-DD")),
    };
    postDataNoTImeOut('eod/reports', data_post)
    .then((res) => {
      console.log(res.data);
      if (res.data.length === 0) {
        ToastNotification("info", "Data Yang Anda Cari Tidak Ada");
        this.setState({
          isLoading: false,
          export: false,
        });
      } else {
        localStorageencryp("tgl_laporan", JSON.stringify(data_post));
        ToastNotification("success", "Laporan EOD Tersedia");
        this.setState({
          LaporanEod: res.data,
          isLoading: false,
          export: true,
        })
      }
    })
    .catch((error) => this.errorHandling(error));

    // console.log(data_post);
  }
  errorHandling(err) {
    ToastNotification('info', err.response?.data?.message || "Terjadi Kesalahan Saat Request Data")
    this.setState({
      isLoading: false,
      export: false,
    })
  }
  render() {
    return (
      <div>
        <ol className="breadcrumb float-xl-right">
          <li className="breadcrumb-item">
            <Link to="#">Laporan</Link>
          </li>
          <li className="breadcrumb-item active">Laporan Eod</li>
        </ol>
        <h1 className="page-header">Laporan EOD </h1>
        <Panel>
          <PanelHeader>Laporan EOD</PanelHeader>
          <br />
          {/* Blank Page */}
          <div className="container">
            <FilterLaporanEod
              isLoading= {this.state.isLoading}
              export= {this.state.export}
              onSubmit={(data) => this.handleSubmit(data)}
              LaporanEod={this.state.LaporanEod} />
          </div>
          <br />
          {/* End Tambah Blank Page  */}
        </Panel>
      </div>
    );
  }
}

FormLaporanEod = reduxForm({
  form: "FormLaporanEod",
  enableReinitialize: true,
})(FormLaporanEod);
export default connect()(FormLaporanEod);